const Docs = () => import('~/components/API/Docs')
export default {
  layout: 'api', 
  components: { Docs },
  async asyncData({ $axios, params }) {
    let post = {}
    try {
      post = await $axios.$get('/docs/' + params.url)
    } catch (e) { console.log(e) }
    return {
      post: post
    }
  }
}
